}

#addTaskDialog,
#addProjectDialog {
  width: 80%;
  margin: 5em auto;
  width: clamp(300px, 80%, 475px);
  min-width: max-content;
  padding: 0;
  border: none;
  border-radius: 10px; 
  isolation: isolate;
  animation: .3s cubic-bezier(.4,0,.2,1) modal-dialog;
  max-width: 100%;
  transition: width .2s ease-in-out, box-shadow .5s;
  box-shadow: 0 2px 8px rgba(0, 0, 0, .16);
  overflow: hidden;
  outline: none !important;
}



/* @keyframes modal-dialog {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
} */

#addTaskDialog::backdrop,
#addProjectDialog::backdrop {
  background-color: rgba(0, 0, 0, .4);
}

.form-section {
  max-height: calc(100vh - 1 * var(--modal-padding-top));
  width: 100%;
  height: 100%;
  overflow: auto;
}

.form {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.form .form-header {
  padding: var(--main-spacing-small);
  padding-left: var(--main-spacing-large);
}

.form .form-header-flex {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  gap: var(--main-spacing-large)
}

.form .form-header-title-container {
  flex-grow: 1;
  min-width: 0;
  flex-basis: auto;
  height: 100%;
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
}

h1.form-header-text {
  font-size: calc(var(--font-size-subtitle) + 1px);
  font-weight: var(--font-weight-medium);
  color: var(--content-primary);
}

.form .form-close-container {
  flex-basis: content;
  min-width: 0;
  flex-grow: 0;
  flex-shrink: 0;
  display: flex;
  align-items: center;
  height: 32px;
}

.form .form-close-button {
  transition-property: box-shadow, border;
  transition-duration: .3s;
  transition-timing-function: ease;
  width: var(--button-height);
  height: var(--button-height);
  padding: 0;
  color: var(--text-secondary-color);
  background-color: transparent;
  outline: none !important;
}

.form .form-close-button:hover {
  background-color:rgba(0, 0, 0, .04);
}

.form .form-close-button svg {
  pointer-events: none;
}

.form-header-line {
  border: 0;
  border-bottom: 1px solid #eee;
}

.form .main-form {
  overflow: auto;
  flex-grow: 0;
}

.form .main-form-container-flex {
  padding: var(--main-spacing-large);
  padding-bottom: calc(var(--main-spacing-xlarge) + 5px);
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  height: 100%;
  width: 100%;
  gap: calc(var(--main-spacing-large) + 1px);
}

.form .main-form-container-flex * {
  outline: none !important;
}

.form .form-row {
  display: flex;
  flex-direction: column;
  width: 100%;
  gap: var(--main-spacing-xsmall);
  flex-grow: 1;
}

.form .form-row label {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  padding-bottom: 2px;
  line-height: 21px;
  letter-spacing: -.10px;
  font-weight: 700;
}

.form .form-row input,
.form .form-row select {
  --main-input-wrapper-height: 32px;
  border-radius: 5px;
  border: 1px solid #e6e6e6;
  overflow: clip;
  height: 32px;
  display: flex;
  align-items: center;
  transition-property: box-shadow, border;
  transition-duration: .3s;
  transition-timing-function: ease;
  padding: calc((30px - 14px - 2px) / 2) 8px;
  text-overflow: ellipsis;
  background: 0 0;
  font-size: var(--font-size-body);
  line-height: calc(var(--font-size-body) + 7px);

}

.form .form-row select {
  padding: 0 4px;
}

.form-buttons-line {
  border: none;
  border-bottom: 1px solid #eee;
}

.form .form-buttons-flex {
  padding: var(--main-spacing-large);
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: flex-end;
  gap: var(--main-spacing-large);
}

.form .form-buttons-flex-2 {
  display: flex;
  gap: 10px;
  justify-content: flex-end;
  flex-wrap: nowrap;
  flex-grow: 1;
  flex-shrink: 0;
}

.form footer .button {
  min-width: 68px;
  color: #444;
  background-color: rgba(0, 0, 0, .04);
  text-align: center;
  padding: 0 12px;
  font-size: 13px;
  height: 32px;
  width: auto;
  border-radius: 5px;
}

.form footer .button:hover {
  background-color: rgba(0, 0, 0, .07);
}

.form footer .submit-button {
  color: #fff;
  background-color: var(--lighter-red);
}

.form footer .submit-button:hover {
  background-color: var(--darker-red);
}

/* Projects page CSS */

.project-page-list-header-container {
  position: sticky;
  position: -webkit-sticky;
  top: 56px, 63px;
  padding-left: var(--main-container-side-padding);
  padding-right: var(--main-container-side-padding);
  padding-top: var(--main-spacing-small);
}

.projects-page-list-blank-space {
  max-width: var(--editor-max-width);
  width: 100%;
  margin: 0 auto;
  padding-top: var(--main-spacing-xxlarge);
  padding-bottom: var(--main-spacing-xsmall);
}

.projects-page-list-header {
  margin-top: var(--main-spacing-medium);
   margin-bottom: var(--main-spacing-large);
  border-bottom: 1px solid #eee;
  max-width: var(--editor-max-width);
  width: 100%;
  margin: 0 auto;
}

.projects-page-list-header-flex {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.projects-page-list-counter {
  display: flex;
  min-width: 0;
  flex-basis: auto;
  flex-grow: 1;
  height: calc(var(--button-height) + 10px);
  align-items: center;
}

.projects-list-counter-text {
  font-weight: var(--font-weight-medium);
  font-size: 0.9rem;
}

.project-page-add-project-button-container {
  display: flex;
  margin-left: var(--main-spacing-medium);
  /* margin-right: var(--main-spacing-medium); */
}

.add-project-button {
  min-width: 60px;
  background-color: var(--sidebar-item-hover);
  height: var(--button-height);
  font-size: var(--button-font-size);
  min-height: 1.9em;
  border: var(--sidebar-item-hover);
  padding: 0 0.8em;
  color: rgb(72, 72, 72);
  font-size: 0.95rem;
}

.add-project-button:hover {
  background-color: var(--sidebar-item-darker-hover);
}

.project-page-list-of-projects-container {
  padding-left: var(--main-container-side-padding);
  padding-right: var(--main-container-side-padding);
  padding-top: var(--main-spacing-medium);
}

.project-page-list-of-projects-container * {
  scroll-margin-top: calc(var(--main-view-header-height) + var(--main-spacing-xsmall));
}

.project-page-projects-ul {
  max-width: var(--editor-max-width);
  width: 100%;
  margin: 0 auto;
  padding: 0;
  list-style: none;
}

.project-page-project-li {
  padding-left: 0;
  scroll-margin-top: calc(var(calc(56px + 0) + var(--main-spacing-xsmall)));
  display: flex;
  position: relative;
  align-items: center;
}

.project-page-project-flex {
  min-width: 0;
  display: flex;
  position: relative;
  flex-grow: 1;
  border-radius: 10px;
  padding-right: (var(--main-spacing-medium));
}

.project-page-project-item {
  flex-grow: 1;
  display: flex;
  align-items: center;
  padding: var(--main-spacing-medium);
  border-radius: 10px;
  text-decoration: none;
}

@media (max-width: 992px) {
  .project-page-project-item  {
      min-width: 175px;
  }
}

.project-page-project-item-div {
  scroll-margin-top: calc(var(--main-view-header-height) + var(--main-spacing-xsmall));
  /* gap: var(--main-spacing-xsmall); */
  display: flex;
  flex-direction: column;
  width: 100%;
}

@media (max-width: 992px) {
  .project-page-project-item-text  {
      min-width: 250px;
  }
}
.project-page-project-item-text {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: flex-start;
  gap: var(--main-spacing-xsmall);
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  padding-right: var(--main-spacing-xsmall);
  font-weight: normal;
}

.project-page-list-actions-container {
  min-width: 24px;
  /* max-width: 24px; */
  margin-right: 5px;
  margin-left: var(--main-spacing-medium);
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.project-page-list-actions-container button {
  opacity: 0;
}

.project-page-project-li:hover .project-page-list-actions-container button {
  opacity: 1;
}

.project-page-list-actions-container>button, .project-page-list-actions-container a {
  color: var(--text-secondary-color);
  font-size: calc(var(--button-font-size) - 2px);
  cursor: pointer;
  height: 24px;
  margin-left: var(--main-spacing-xsmall);
  border-radius: 3px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition-duration: .1s; /* Faster transition */
} 

.project-page-list-delete-button {
  color: var(--lighter-red) !important;
}

.project-page-project-flex:hover {
  background: rgba(0, 0, 0, .04);
}

.project-page-project-flex:hover:not(:focus-visible):before {
  content: "";
  background: #fff;
  width: 100%;
  height: 1px;
  display: block;
  position: absolute;
  top: -1px;
  left: 0;
}

.project-page-project-flex:hover:not(:focus-visible):after {
  top: auto;
  bottom: -1px;
  content: "";
  background: #fff;
  width: 100%;
  height: 1px;
  display: block;
  position: absolute;
  left: 0;
}

.project-page-list-actions-container > button:hover {
  color: var(--main-button-hover-text);
  background-color: var(--main-button-hover-background);
}

.footer {
  text-align: center;
  padding-bottom: 1em;
}

.footer a {
  color: var(--darker-red);
  text-decoration: none;
}

@media screen and (max-width: 46.875em) {
  .app-sidebar-container {
    margin-left: calc(-1 * 280px);
    transition: margin-left .3s cubic-bezier(.4,0,.2,1);
    display: flex;
    position: relative;
  }

  .project-item .task-list-actions-container button,
  .project-page-list-actions-container>button {
    opacity: 0.8;
  }

  .edit-project-button, .delete-project-button {
    font-size: calc(var(--button-font-size) - 2px);
  }
